<template>
  <div>
    <ul class="footer" :style="{background:bg}">
      <li>
        <router-link to="/movie/movieList">
            <img src="../movie/images/vid.png" class="a">
            <img src="../movie/images/电影1.png" class="b">热映
        </router-link>
      </li>
      <li>
        <router-link to="/musiclist">
          <img src="../movie/images/影院A.png" class="a">
          <img src="../movie/images/tubiao_03.png" class="b">影院
        </router-link>
      </li>
      <li>
        <router-link to="/photolist">
          <img src="../movie/images/tubiao_052.png" class="a">
          <img src="../movie/images/tubiao_05.png" class="b">我的
        </router-link>
      </li>
     

    </ul>
  </div>
</template>

<script>
  export default {
    props: ['bg'],

  }

</script>

<style scoped>
  .footer .router-link-active .a {
    display: block ;
  }

.footer .router-link-active .b {
    display:none;
  }
 .b{
     display: block;
 }
 .a{
     display: none;
 }
  .footer {
    width: 100%;
    height: 1rem;
    background: #fff;
    position: fixed;
    bottom: 0;
    line-height: 1rem;
    display: flex;
    justify-content: space-around;
  }

  .footer li {
    width: 33%;
    text-align: center;
    
  }

  .footer li {
    width: 40px;
    height: 21px;
    position: relative;
  }

  
  .footer li a {
    width:100%;
    text-align: center;
    line-height: 1.5rem;
    color: #777;
    position: relative;
    
    
  }
    .footer li a img{
    position: absolute;
    padding-top: 20%;
    left: 10%;
    
    }

  .footer li a.router-link-active {
    color: #fd5d73;
  }

</style>



