<script>
import Axios from 'axios'
import CommonHeader  from '../common/Header'
import CommonFooter from'../common/Footer'
export default {
    data(){
        return{
            MusicList:[],
            bg:false,
            bt:false,
            bn:false,
        };
    },mounted(){
        let url = "/static/data/yingyuan.json";
        Axios.get(url)
      .then(res => {
        this.MusicList = res.data.data; //获取api数据.
        console.log(res.data.data);
      })
      .catch(() => {
        alert("数据接收失败!");
      });

      
    },methods:{
        bb:function(){
            this.bg=!this.bg;
        },
        bc:function(){
            this.bt=!this.bt;
        },
        br:function(){
            this.bn=!this.bn;
        }
    },
    components:{
        
        CommonFooter
    }
}
</script>

