<template>
    <div>
        <span id="p1">选择城市<router-link to="/" class="a1">首页</router-link></span>
        
        <a href="/" v-for="(movie,index) in City" :key="index">{{movie.n}}</a>
        <div id="bottom">
            <img id="img1" src="./images/定位3.png" alt="">
            <span id="dd">定位失败,请稍后从试</span>
            <img id="img2" src="./images/刷新.png" alt="">
        </div>
    </div>
</template>

<script>

import Axios from 'axios'
export default {
    data(){
        return{
            City:[]
        };
    },mounted(){
      let url1 = "/static/data/city.json"; //url2 => 本地数据.

    Axios.get(url1)
      .then(res => {
        this.City = res.data.city; //获取api数据.
        console.log(this.City);
        console.log(res);
      })
      .catch(() => {
        alert("数据接收失败!");
      });
    
     window.ontouchmove = function(e){
                var oBom = document.getElementById('bottom');
                var touch = e.touches[0];
                var x = Number(touch.clientX);
                var y = Number(touch.clientY);
                //console.log("当前触摸点的横坐标"+x+"当前触摸点的纵坐标"+y);
            
                if(this.ontouchmove){
                    oBom.style.display="none";
                    //console.log(event)
                document.ontouchend = function(){
                     if(this.ontouchend){
                    oBom.style.display="block"
                }
                };
                
                   
                
                }}

}}
</script>

<style scoped>
#p1{display: block; width: 100%;height: 1rem;background:#ff4d64;position: fixed;top: 0;text-align: center;line-height: 1rem;font-size: 1.4em;color: #fff;}
a:nth-of-type(1){margin-top: 1rem;}
a{border-bottom: 1px solid rgba(133, 133, 133,.1);height: .7rem;line-height: .7rem;width: 95%;float: right;color: #777;}

.a1{color: #fff;position: absolute;left: 5%;font-size: .7em;}

#bottom{width: 93.8%;height: 35px;background:hsla(0,0%,94%,.9);border-radius: 5px;position: fixed; bottom: 10px;left: 3.2%;}
#bottom #img1{width: 14px;height: 14px;float: left;margin: 12px 0 0 2.5%;}
#bottom #dd{display: block; color: #666;line-height: 35px;font-size: 13px;margin-left: 5px;float: left;}
#bottom #img2{width: 15px;height: 15px;float: right;margin: 12px 3% 0 0;}
</style>



