<template>
  <div class="header" :style="bg">
    <router-link to="/city">
      <span class="home" @click="goHome">
        <img src="../movie/images/taopiaopiao_03.png" alt="">城市▼</span></router-link>
    <!-- <h2 class="title"><slot name="title">aa</slot></h2> -->
    <ul class="rit">
      <li>
        <router-link to="/movie/movieList"><img src=""><b>正在热映</b></router-link>
      </li>
      <li>
        <router-link to="/movie/movieHeat"><img src=""><b>即将上映</b></router-link>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    methods: {
      goHome() {
        this.$router.push('/')
      }
    },
    props: ['bg']
  };

</script>

<style scoped>
  .header {
    width: 100%;
    height: 1rem;
    background: #fff;
    position: fixed;
    z-index: 9;
    top: 0;
    line-height: 1rem;
  }

  .home {
    color: #242424;
    margin-left: 0.4rem;
  }

  .home img {
    margin: 0.25rem 0.2rem 0 0;
  }

  /* .title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #242424;
} */
  .rit {
    position: absolute;
    top: 0%;
    right: 8%;
  }

  .rit li {
    float: left;
    font-size: 1em;
    margin-right: 0.2rem;
  }

  .rit li a {
    color: #777;
  }

  .rit li a.router-link-active {
    color: #ff4d64;
  }

</style>



