<template>
    <div>
        <common-header ></common-header>
        <swipe class="my-swipe">
          <swipe-item class="slide1"></swipe-item>
          <swipe-item class="slide2"></swipe-item>
          <swipe-item class="slide3"></swipe-item>
        </swipe>
        <div class="con">
          <ul class="movie-List" v-for="movie in movieList" :key="movie.id">
            <li class="movie" @click="chan(movie.id)">
              <!-- <router-link to="/movieDetail"> -->
              <div class="img">
                <img :src="movie.img">
              </div>
              <div class="right">
                <p class="p1">{{movie.nm}}</p>
                <!-- <p>{{movie.ver}}</p> -->
                <span class="s1">淘票票评分:</span><span class="s2">{{movie.sc}}</span>
                <p>导演:{{movie.dir}}</p>
                <p>主演:{{movie.star}}</p>
              </div>
              <div class="a1">
                  <a href="">购票</a>
              </div>
              <!-- </router-link> -->
            </li>
          </ul>
        </div>
        <common-footer bg="#f0cd3f"></common-footer>
    </div>
</template>

<script>
import Axios from "axios";

import CommonHeader from '../common/Header'
import CommonFooter from '../common/Footer'
import 'vue-swipe/dist/vue-swipe.css'
import { Swipe, SwipeItem } from 'vue-swipe';

export default {
  data(){
    return{
      movieList:[]
    };
  },
  
    mounted(){
      let url2 = "/static/data/moviedata.json"; //url2 => 本地数据.

    Axios.get(url2)
      .then(res => {
        this.movieList = res.data.data.movies; //获取api数据.
        console.log(res.data.data.movies);
      })
      .catch(() => {
        alert("数据接收失败!");
      });
    },
    components: {
        CommonHeader,
        CommonFooter,
        'swipe': Swipe,
        'swipe-item': SwipeItem
    },
    methods:{
      chan(movieId){
        this.$router.push('/movieDetail/'+movieId);
      }
    }
    
}

</script>
<style scoped>
.con{margin-bottom: 1rem;}

.my-swipe {
  height: 155px;
  color: #fff;
  font-size: 30px;
  text-align: center;
  margin-top: 1rem;
}

.slide1 {
  background:url('./images/TB1wJMaowHqK1RjSZJnXXbNLpXa-1280-520.jpg_720x720Q30s100.jpg')no-repeat;
  background-size: 100% 100%;
  color: #fff;
}

.slide2 {
  background:url('./images/TB1ibiQpVzqK1RjSZFoXXbfcXXa-1280-520.jpg_720x720Q30s100.jpg')no-repeat;
  background-size: 100% 100%;
  color: #000;
}

.slide3 {
  background:url('./images/TB1HYgyoYrpK1RjSZTEXXcWAVXa-1280-520.jpg_720x720Q30s100.jpg')no-repeat;
  background-size: 100% 100%;
  color: #fff;
}
.movie-List{padding: 15px 15px 0 15px;height: 115px;}
.movie a{outline: none;}
.img{flex: 1 1 0;margin-right: 0.2rem;width: 18.8%;height: 82.6%;float: left;}
.right{width:60.6%;height: 82.6%;float: left; }
.right .p1{display: block; color: #000;width: 128px;height: 24px;box-sizing: border-box;font-weight: 500px;overflow: hidden;text-overflow: ellipsis;font-size: 1.4em;line-height: 24px;}
.right p{overflow: hidden;text-overflow: ellipsis;width:100%;white-space: nowrap;color: #777;font-size: 13px;}

.s1{font-size: 13px;color: #777;}
.s2{color: #fea54c;}


.a1{width:14.5%;height: 72.6%; float: left;}
.a1 a{width: 50px;height: 28px;display: block;background: #ff5872;color: #fff;text-align: center;line-height: 28px;border-radius: 12px;margin-top: 100%;}


</style>


