<template>
    <div>
        <div class="top">
            <p class="p1">近期最受期待</p>
            <ul class="top1">
                <li v-for="movie in MovieHeat" :key="movie.id">
                    <img :src="movie.img" @click="chang(movie.id)">
                    <h5 class="name">{{movie.nm}}</h5>
                    <p class="time">{{movie.rt}}</p>
                </li>
            </ul>
        </div>
        
       <div class="con">
          <ul class="movie-List" v-for="movie in MovieHeat" :key="movie.id">
            <li class="movie" @click="chang(movie.id)">
              <!-- <router-link to="/movieDetail"> -->
              <div class="img">
                <img :src="movie.img">
              </div>
              <div class="right">
                <p class="p1">{{movie.nm}}</p>
                <!-- <p>{{movie.ver}}</p> -->
                <span class="s2">{{movie.wish}}</span><span class="s1">人想看</span>
                <p>导演:{{movie.dir}}</p>
                <p>主演:{{movie.star}}</p>
              </div>
              <div class="a1">
                  <a href="">预售</a>
              </div>
              <!-- </router-link> -->
            </li>
          </ul>
        </div>
        
        
    </div>
</template>

<script>
import Axios from 'axios'
import CommonHeader  from '../common/Header'
import CommonFooter from'../common/Footer'
export default {
    data(){
        return{
            MovieHeat:[]
        };
    },mounted(){
         let url = "/static/data/moviedata1.json"; //url2 => 本地数据.

    Axios.get(url)
      .then(res => {
        this.MovieHeat = res.data.data.movies; //获取api数据.
        console.log(res.data.data.movies);
      })
      .catch(() => {
        alert("数据接收失败!");
      });
    },
    components:{
        CommonHeader,
        CommonFooter
    }, methods:{
      chang(movieId){
        this.$router.push('/movieDetail/'+movieId);
      }
    }
}
</script>

<style scoped>
.top{width: 93%;height:192px;padding:1.8vh 3vw 1.8vh 4vw ;margin-top: 1rem;}
.top1{overflow-y:hidden;overflow-x:scroll;white-space: nowrap;margin-top: 10px;border-bottom: 12px solid rgb(238, 238, 238);}
.top1::-webkit-scrollbar{display:none}
.top li{overflow: hidden;display: inline-block;width: 85px;height: 100%;margin-right: 15px;}
.top .p1{font-size: 14px;height: 19px;line-height: 19px;color: rgb(51, 51, 51)}
.top img{flex: 1 1 0;margin-right: 0.2rem;width: 85px;height: 115px;}
.top .name{font-size: 13px;font-weight: 500px;overflow: hidden;text-overflow: ellipsis;height: 16px;line-height: 16px;width: 100%;margin-top: 5px;}
.top .time{width: 100%;height: 14px;line-height: 14px;font-size: 12px;color: rgb(153, 153, 153);margin-top: 5px;}


.con{margin-bottom: 1rem;}
.movie-List{padding: 15px 15px 0 15px;height: 115px;}
.movie a{outline: none;}
.img{flex: 1 1 0;margin-right: 0.2rem;width: 18.8%;height: 82.6%;float: left;}
.right{width:60.6%;height: 82.6%;float: left; }
.right .p1{display: block; color: #000;width: 128px;height: 24px;box-sizing: border-box;font-weight: 500px;overflow: hidden;text-overflow: ellipsis;font-size: 1.4em;line-height: 24px;}
.right p{overflow: hidden;text-overflow: ellipsis;width:100%;white-space: nowrap;color: #777;font-size: 13px;}

.s1{font-size: 13px;color: #777;}
.s2{color: #fea54c;}


.a1{width:14.5%;height: 72.6%; float: left;}
.a1 a{width: 50px;height: 28px;display: block;background: #55c2ff;color: #fff;text-align: center;line-height: 28px;border-radius: 12px;margin-top: 100%;}


</style>



