<template>
  <div>
    <div class="top">
      <p class="pm">{{MovieDetail.nm}}</p>

    </div>
    <div class="con">
      <img :src="MovieDetail.img">
      <div class="con1">
        <p>{{MovieDetail.cat}}/{{MovieDetail.dur}}分钟</p>
        <br>
        <p>{{MovieDetail.rt}}</p>
        <br>
        <p class="p1"><b>{{MovieDetail.vnum}}万人想看</b></p>
        <p>/大V推荐</p>
      </div>
    </div>
    <div class="con2">
      <div id="left">
        <div class="score">{{MovieDetail.sc}}</div>
        <div class="stars">★★★★★</div>
        <div class="tsc">淘票票評分</div>
        <div class="ren">{{MovieDetail.dur}}人评&nbsp;&nbsp;▶</div>
      </div>
      <ul id="right">
        <li>★★★★★
          <div></div>
        </li>
        <li>★★★☆☆
          <div></div>
        </li>
        <li>★★☆☆☆
          <div></div>
        </li>
        <li>★☆☆☆☆
          <div></div>
        </li>
        <li>☆☆☆☆☆
          <div></div>
        </li>
      </ul>

    </div>

      <div class="caidan">
        <div class="caidan1">彩蛋</div>
        <p>片尾字幕进行中和结束后</p>
      </div>
    <ul class="jianjie">
      <li><a>简介</a></li>
      <li>影评</li>
      <li>更多</li>
    </ul>

    <div id="jianjie">
      <p>{{MovieDetail.dra}}</p>
    </div>
    <div class="comments">
      <h2>观众热评</h2>
      <div class="pingjia" v-for="pingjia in List " :key="pingjia.id">
        <img :src="pingjia.avatarurl" alt="">
        <p class="p1">{{pingjia.nick}}</p>
        <p class="p2">评价:{{pingjia.score}}分</p>
        <br>
        <p class="p3">{{pingjia.content}}</p>
      </div>
    </div>
    <router-link to="" id="gp">选座购票</router-link>
  </div>
</template>

<script>
  import Axios from 'axios'
  export default {
    data() {
      return {
        MovieDetail: [],
        List:[]
      };
    },
    mounted() {
      let url1 = "/static/data/moviedetail" + this.$route.params.movieId + ".json"; //url2 => 本地数据.

      Axios.get(url1)
        .then(res => {
          this.MovieDetail = res.data.data.MovieD; //获取api数据.
          this.List=res.data.data.CommentResponseModel.hcmts;
          console.log(this.List);
          console.log(this.MovieDetail);
          // console.log(this.MovieDetail.nm)
          // console.log(res);
        })
        .catch(() => {
          alert("数据接收失败!");
        });
    },

  }

</script>

<style scoped>
  .top {
    width: 100%;
    height: 40vw;
    background: #406390;
  }

  .pm {
    font-size: 1.37em;
    font-weight: 600;
    width: 67%;
    float: right;
    line-height: 1.4em;
    margin-top: 21.5vw;
    color: #fff;
  }

  .con {
    height: 78px;
    background: #fff;
    border-radius: 5px 5px 0 0;
    margin-top: -4%;
    border-bottom: 1px solid rgba(231, 231, 231, .7);
  }

  .con img {
    width: 23.4vw;
    height: 34.4vw;
    border: 2px solid #fff;
    border-radius: 4px;
    margin: -58px 0 0 5%;
    float: left;
  }

  .con1 {
    padding-top: 2.7vw;
  }

  .con1 p {
    font-size: .8em;
    width: auto;
    margin-left: 2vw;
    float: left;
    color: #999;
    line-height: 1.1;
    padding-bottom: 2.1vw;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .con1 .p1 {
    color: #000;
  }

  .con1 p:nth-child(1) {
    letter-spacing: 1px;
  }

  .con2 {
    height: 72px;
    width: 89vw;
    padding: 7px 5vw 7px 5vw;
  }
  #left{
      width: 50%;
      height: 100%;
      float: left;
      }

  #left .score{
      font-size: 35px;
      box-sizing: border-box;
     height: 40px;
     width:48.6px ;
     line-height: 40px;
     float: left;
  }
  #left .stars{
      font-size: 16px;
      color: #fea348;
      width: 80px;
      height: 40px;
      line-height: 40px;
      float: left;
      margin-left: 10px;
  }
  #left .tsc{font-size:13px;widows: 65px;height: 20px;line-height: 20px;float: left; color: #000;font-weight: 700;}
  #left .ren{font-size: 12px;color: rgb(119, 119, 119);float: left;width:50%;height: 20px;line-height: 20px;margin-left: 3%;}


  #right{
      width: 50%;
      height: 100%;
      float: left;
  }

  #right li{
    height: 10px;
    line-height: 10px;
    margin-top: 2px;
    
  }
  #right li div{
    width: 60px;
    height: 3px;
    background: #000;
    display: inline-block;
    margin-bottom: 4px;
  }
  #right li:nth-of-type(2) div{
    width: 40px;
    height: 3px;
    background: #000;
    display: inline-block;
    margin-bottom: 4px;
  }
  #right li:nth-of-type(3) div{
    width: 30px;
    height: 3px;
    background: #000;
    display: inline-block;
    margin-bottom: 4px;
  }
  #right li:nth-of-type(4) div{
    width: 10px;
    height: 3px;
    background: #000;
    display: inline-block;
    margin-bottom: 4px;
  }
  #right li:nth-of-type(5) div{
    width: 0px;
    height: 3px;
    background: #000;
    display: inline-block;
    margin-bottom: 4px;
  }

  .caidan{
    width: 90%;
    height: 50px;
    margin-left: 5%;
    border-bottom: 1px solid rgb(243, 243, 243);
    
  }
  .caidan .caidan1{
    width: 32px;
    height: 17px;
    line-height: 17px;
    font-size: 10px;
    border: 1px #ccc solid;
    border-radius: 4px;
    text-align: center;
    line-height: 17px;
    float: left;
    margin-top: 16px;
  }
  .caidan p{
    width: 70%;
    height: 17px;
    line-height: 17px;
    font-size: 14px;
    padding-left: 10px;
    float: left;
    margin-top: 17px;
  }
  .jianjie{width: 100%;justify-content: 46px;}
  .jianjie li{width: 33.3%;line-height: 46px;font-size: 14px;text-align: center;float: left;border-radius: 5px;}
  .jianjie li a{border-bottom: 1px solid #000;padding-bottom: 8px;}

  #jianjie p{width: 90%;height: auto;margin-left: 5%;text-indent: 1em;font-size: 14px;color: rgb(104, 104, 104);border-bottom: 1px solid rgb(212, 212, 212);padding-bottom: 5px;}

  .comments{width: 90%;margin-left: 5%;margin-bottom: 1rem;}
  .comments h2{font-size: 16px;padding: 5px 0 10px 0;}
  .pingjia{margin-top: 20px;}
  .comments .pingjia img{width: 50px;height: 50px;border-radius: 25px;padding-right: 15px;float: left;}
  .comments .pingjia .p1{width: 50%;height: 22px;font-weight: 700;}
  .comments .pingjia .p2{height: 22px;color: #ff8d1b;font-weight: 700;font-size: 14px;}
  .comments .pingjia .p3{color: rgb(114, 114, 114);text-indent: 1em;}





  #gp {
    width: 100%;
    height: .9rem;
    background: #ff5770;
    position: fixed;
    left: 0;
    bottom: 0;
    color: #fff;
    font-size: 1.3em;
    text-align: center;
    line-height: .9rem;
  }


</style>



